@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 70%;
  height: 10vh;
  width: 50%;
  margin-left: 25%;
  margin-right: 25%;
  border-bottom: 1px solid #f2f2f2;
  background: rgb(241, 53, 53);
  background: linear-gradient(90deg, rgba(241, 53, 53, 1) 0%, rgba(252, 125, 86, 1) 49%, rgba(241, 53, 53, 1) 100%);
  color: #fff;
}

.navbar h1 {
  color: #fff;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  width: 100%;
  text-decoration: none;
  padding: 6px;
  color: #fff;
  font-weight: bold;
}

.navbar a:hover {
  color: #fc7d56;
  background-color: #fff;
  border-radius: 2px;
}

.content {
  max-width: 70%;
  height: 80vh;
  width: 50%;
  margin-left: 25%;
  margin-right: 25%;
  height: 100%;
  margin: 30px auto;
  padding: 10px;
  overflow-y: auto;
}

/* blog previews / list */
.blog-preview {
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 5px 5px;
  margin: 10px 0;
  border-bottom: 1px solid #fafafa;
  border-radius: 2px;
}

.blog-preview:hover {
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.1);
}

.blog-preview h2 {
  font-size: 20px;
  color: #fc7d56;
}

button {
  background-color: rgb(55, 54, 54);
  color: #fff;
  padding: 0.5em;
  width: 30%;
  border: none;
  cursor: pointer;
  font-weight: bold;
  margin-bottom: 0.5rem;

}

button:hover {
  background-color: #f13535;
  color: #fff;
}

h1 {
  font-weight: 800;
  font-size: 3em;
}

p {
  font-weight: 500;
  margin-top: 1rem;
}

.about-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}

.image-div {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.about-image {
  width: auto;
  height: 300px;
}

.about-main {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.side-image {
  width: 100%;
  height: 100%;
}

.side-details {
  flex: 1;
}

.main-details {
  flex: 2;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 10vh;
  bottom: 0;
  background-color: rgb(55, 54, 54);
}

.footer-details {
  padding: 5em;
  margin-left: 10%;
  margin-right: 10%;
  width: 80%;
  gap: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.footer-logo h1 {
  color: #5f5959;
}

.footer-links Links {
  color: #f13535;
}

/* create new blog form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.create label {
  text-align: left;
  display: block;
}

.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}

.create input,
.create textarea,
.create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}

.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

.blog-author {
  font-weight: 800;
  color: #818183;
}

.loading-message {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}

.error-message {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: 700;
}

.not-found {
  display: flex;
  width: 100%;
  height: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  gap: 0.5rem;
}